// imports top
@import "abstract/config";
@import "bootstrap/bootstrap-grid";
@import "abstract/normalize";
@import "abstract/reset";
@import "typography/typography";
@import "typography/icomoon";
@import "plugins/jquery-ui";
@import "plugins/select2.min";
@import "plugins/swiper-bundle.min";
@import "plugins/plugins";
@import "components/site";
@import "components/navigation";
@import "components/days";
@import "components/categories";
@import "components/availability";
@import "components/form";
@import "components/modal";
@import "components/login";

body,
html {
  height: 100%;
}

body {
  color: var(--text-color);
  font: 16px/1.3 var(--primary-font);

  &.login {
    background: var(--pattens-blue);
  }

  &.no-scroll {
    overflow: hidden;
  }
}

.wrapper {
  max-width: 1920px;
  min-height: 100%;
  margin: 0 auto;
  flex-flow: column nowrap;
  display: flex;
}

.header {
  padding: 15px 25px;
  border-bottom: 1px solid var(--border-color);
  align-items: center;
  display: flex;
}

.body {
  display: flex;
  flex: 1 0 auto;
}

.aside {
  padding: 25px 25px 5px 25px;
  align-self: flex-start;
  flex: 0 0 326px;
}

.content {
  width: calc(100% - 326px);
  border-left: 1px solid var(--border-color);
  padding: 25px 25px 5px 25px;
  isolation: isolate;
}

// imports bottom
@import "tablet";
@import "mobile";
@import "abstract/helpers";

input:read-only {
  font-weight: bold;
  background-color: gray;
}